import { useState } from "react";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import CreateButton from "../../../../Ui/Button/CreateButton";
import { useTranslation } from "react-i18next";
import style from "../../../../UserStaffMember/FinanceManager/Income/Income.module.css";
import { useAcademicDepartmentMapSubjectMaster } from "../../../../Hooks/ApiHooks/AcademicDepartmentManager/academic-department-manager-api";
import InstituteDepartment from "../../../../UserStaffMember/InstituteDepartment/InstituteDepartment";
import InstituteSubject from "../../../../UserStaffMember/InstituteSubject/InstituteSubject";

const MapInstituteAcademicDepartmentMaster = ({
  onClose,
  onRefetch,
  did,
  instituteId,
}) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [step, setStep] = useState("DEPARTMENT");
  const [state, setState] = useState({
    subId: "",
    mapId: "",
    selectedDepartment: "",
  });
  const [academicDepartmentMapSubjectMaster] =
    useAcademicDepartmentMapSubjectMaster();

  const onAdd = () => {
    if (did && state?.subId?._id) {
      setDisabled((pre) => !pre);
      academicDepartmentMapSubjectMaster({
        bodyParameter: {
          did: did,
          subId: null,
          // subId: state.subId?._id,
          mapId: state.subId?._id,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  const onCloseStep = () => {
    if (step === "DEPARTMENT") {
      onClose();
    } else if (step === "MASTER") {
      setStep("DEPARTMENT");
    } else {
      setStep("MASTER");
    }
  };
  const onSelectDepartment = (dt) => {
    setState((prev) => ({
      ...prev,
      selectedDepartment: dt,
    }));
    setStep("MASTER");
  };
  const onSelectMaster = (dt) => {
    setState((prev) => ({
      ...prev,
      subId: dt,
    }));
    setStep("");
  };

  // console.info("djhgbds", state);
  return (
    <>
      {step === "DEPARTMENT" ? (
        <InstituteDepartment
          onSelectStaff={onSelectDepartment}
          title={t("select_department")}
          onClose={onCloseStep}
          selectedId={state.selectedDepartment?._id}
          instituteId={instituteId}
          whichType="Normal"
        />
      ) : step === "MASTER" ? (
        <InstituteSubject
          onSelectStaff={onSelectMaster}
          title={t("select_subject")}
          onClose={onCloseStep}
          selectedId={state.subId?._id}
          instituteId={state.selectedDepartment?._id}
        />
      ) : (
        <Modal onClose={onCloseStep}>
          <div className={style.modal_container}>
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6>{t("map_subject_master")}</h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
              }}
            >
              <div className={style.modal_container_header}>
                <h6>
                  {/* {t("mapping_subject_master_of")} -{" "} */}
                  <span
                    style={{
                      fontWeight: "600",
                    }}
                  >
                    {state.subId?.subjectName}
                  </span>{" "}
                  ({" "}
                  <span
                    style={{
                      fontWeight: "500",
                    }}
                  >
                    {state?.selectedDepartment?.dName}
                  </span>
                  )
                </h6>
              </div>
              <CreateButton label="map" onAction={onAdd} />
            </UserMemberContentWrapper>
            {disabled && <QvipleLoader />}
          </div>
        </Modal>
      )}
    </>
  );
};
export default MapInstituteAcademicDepartmentMaster;
