import { useTranslation } from "react-i18next";
import { useCallback, useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import { assestsNavbarUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import { useAcademicDepartmentAllSubjectMaster } from "../../../../Hooks/ApiHooks/AcademicDepartmentManager/academic-department-manager-api";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../../Utils/Functions/debounce";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import Loading from "../../../../Loader/Loading";
import InstituteAcademicDepartmentMasterCard from "./InstituteAcademicDepartmentMasterCard";
import CreateButton from "../../../../Ui/Button/CreateButton";
import MapInstituteAcademicDepartmentMaster from "./MapInstituteAcademicDepartmentMaster";
const InstituteAcademicDepartmentMaster = ({
  did,
  instituteId,
  openAs,
  departmentName,
}) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const [add, setAdd] = useState(false);

  const {
    academicDepartmentAllSubjectMaster,
    academicDepartmentAllSubjectMasterLoading,
    academicDepartmentAllSubjectMasterRefetch,
  } = useAcademicDepartmentAllSubjectMaster({
    data: {
      did: did,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !did,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (academicDepartmentAllSubjectMaster?.list?.length) {
      let obj = nextPage(academicDepartmentAllSubjectMaster?.list?.length);
      setState(() => obj);
    }
  }, [academicDepartmentAllSubjectMaster?.list]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };
  const onRefetch = useCallback(() => {
    setPage(() => 1);
    setSearch("");
    academicDepartmentAllSubjectMasterRefetch();
  }, [academicDepartmentAllSubjectMasterRefetch]);

  const onToggle = () => {
    setAdd((pre) => !pre);
  };
  return (
    <>
      <UserMemberContentWrapper>
        {openAs === "DEPARTMENT_HEAD" ? (
          <div className={style.fm_header_container}>
            <h6>{t("masters")}</h6>
            <div className={style.fm_search}>
              <section className={style.fm_search_container}>
                <div className={style.fm_search_container_inner}>
                  <img
                    className={style.fm_search_icon}
                    alt="search icon"
                    src={`${assestsNavbarUrl}/navbar-search.svg`}
                  />
                  <input
                    type="text"
                    placeholder={t("search")}
                    onChange={onSearchEvent}
                  />
                </div>
              </section>
              <CreateButton
                label={t("map_master")}
                customStyle={{
                  margin: "0",
                  padding: "0.4rem 1rem",
                  minWidth: "fit-content",
                }}
                onAction={onToggle}
              />
            </div>
          </div>
        ) : (
          <div className={style.fm_search_full}>
            <section className={style.fm_search_full_container}>
              <div className={style.fm_search_full_container_inner}>
                <img
                  className={style.fm_search_full_icon}
                  alt="search icon"
                  src={`${assestsNavbarUrl}/navbar-search.svg`}
                />
                <input
                  type="text"
                  placeholder={t("search")}
                  onChange={onSearchEvent}
                />
              </div>
            </section>
            <CreateButton
              label={t("map_master")}
              customStyle={{
                margin: "0",
                padding: "0.4rem 1rem",
                minWidth: "fit-content",
              }}
              onAction={onToggle}
            />
          </div>
        )}
      </UserMemberContentWrapper>
      <BorderBottom />
      <UserMemberContentWrapper
        customStyle={{
          paddingTop: "0.3rem",
          paddingBottom: "1rem",
        }}
      >
        {academicDepartmentAllSubjectMaster?.list?.map((master, index) =>
          academicDepartmentAllSubjectMaster?.list?.length === index + 1 ? (
            <div key={master?._id} ref={ref}>
              <InstituteAcademicDepartmentMasterCard
                master={master}
                departmentName={departmentName}
              />
            </div>
          ) : (
            <InstituteAcademicDepartmentMasterCard
              departmentName={departmentName}
              master={master}
              key={master?._id}
            />
          )
        )}
        {academicDepartmentAllSubjectMasterLoading ? (
          search ? (
            <QvipleLoader />
          ) : (
            <Loading />
          )
        ) : search ? (
          academicDepartmentAllSubjectMaster?.list?.length === 0 && (
            <EmptyMessage title="No master found related this search." />
          )
        ) : (
          academicDepartmentAllSubjectMaster?.list?.length === 0 && (
            <EmptyMessage title="No master." />
          )
        )}
      </UserMemberContentWrapper>
      {add && (
        <MapInstituteAcademicDepartmentMaster
          onClose={onToggle}
          instituteId={instituteId}
          onRefetch={onRefetch}
          did={did}
        />
      )}
    </>
  );
};

export default InstituteAcademicDepartmentMaster;
